.title {
  margin: 0;

  font-size: 25px;
  line-height: 30px;
  text-align: center;
  color: $black;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }

  &--main {
    text-align: center;
  }
}
